#figs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#figs button {
  width: 30%;
  height: 30%;
  margin: 0 2px;
}

button img {
  width: 20vw;
  height: 20vw;
  align-self: center;
  margin-bottom: 3pt;
}

#sizes label {
  width: 45%;
}

#sizes input {
  font-size: x-large;
  width: 10ex;
  max-width: 40%;
  margin-left: 1ex;
}

